@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
	background-color: #23a6d5;
	color: #3c4043
}
.mainBlock {
    border-radius: 2rem;
    box-sizing: border-box;
    text-align: center;
    background-color: #ffff;
    opacity: 0.9;
    padding: 2rem;
    width: 100%;
    max-width: 350px;
    margin: 100px auto;
    display: table;
    -webkit-filter: drop-shadow(2px 2px 7px black);
            filter: drop-shadow(2px 2px 7px black);
}

.Error {
    padding: 1rem;
    box-sizing: border-box;
    border-radius: 2rem !important;
    background-color: #feeaea;
    border: 1px solid #fadadb;
    margin-bottom: 1rem;
    border-radius: 3px;
}

.Spinner {
    display: table;
    margin: 0 auto;
}

.regField {
    width: 100%;
    text-align: center;
    box-sizing: border-box;
    background: transparent;
    border: 1px solid #9e9e9e;
    outline: none;
    color: #000;
    padding: 1rem;
    margin-bottom: 5%;
    border-radius: 2rem;
    font-size: 1rem;
}

.submitBtn {
    width: 100%;
    background: #425F9C;
    border: 0;
    color: #fff;
    padding: 1rem;
    margin: 1rem 0 0 0;
    font-size: 1rem;
    cursor: pointer;
    border-radius: 2rem;
}

.linkBtn {
    border: 0;
    background-color: unset;
    text-decoration: underline;
    color: #425f9c;
    padding: 0;
    margin: 1rem 0 0 0;
    font-size: 1rem;
    cursor: pointer;
}
 
  .title {
    font-family: sans-serif;
    font-size: 2rem;
    font-weight: 600;
  }
  
  .subtitle {
    font-family: sans-serif;
    font-size: 1rem;
    font-weight: 600;
    margin: 1rem;
  }
  
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
}

.block {
    box-sizing: border-box;
    text-align: center;
    display: table;
    opacity: .9;
    width: 100%;
    border-radius: 1rem;
    background: linear-gradient(-25deg, #f0f0f0, #ffffff);
    border: 0;
    padding: 2rem;
    font-size: 1rem;
    margin-bottom: 1rem;
    -webkit-filter: drop-shadow(1px 1px 10px black);
            filter: drop-shadow(1px 1px 10px black);
}

.welcomeBlock {
    color: #ffffff;
    display: flex;
    justify-content: space-between;
    margin: 1rem;
    padding: 1%;
    font-size: 2rem;
    text-shadow: 0px 4px 4px #292929;
    border-bottom: 2px solid rgb(207, 207, 207);
    border-bottom-style:inset;
}

.logOut{
    display: block;
    min-width: -webkit-max-content;
    min-width: max-content;
    padding: 5px 10px;
    line-height: 2rem;
    text-decoration: none;
    text-align: center;
    border-radius: 2rem;
    border: 2px solid #ffffff;
    color: rgb(255, 255, 255);
    font-size: 50%;
    font-family: arial;
    position: relative;
    overflow: hidden;
    background: transparent;
    transition: all .35s;
    cursor: pointer;
}
  
.logOut:before,
.logOut:after{
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    top: -100%;
    left: 0;
    background: #ffffff;
    z-index: -1;
    transition: all .35s;
}
  
.logOut:before{
    opacity: .5;
}
  
.logOut:after{
    transition-delay: .2s;
}
  
.logOut:hover{
    color: #000;
}
  
.logOut:hover:before,
.logOut:hover:after{
    top: 0;
}

.reset {
    display: block;
    padding: 5px 10px;
    text-decoration: none;
    text-align: center;
    border-radius: 2rem;
    border: 2px solid #000000;
    color: #696969;
    font-size: 1rem;
    background: transparent;
    cursor: pointer;
}

.totalMoney {
    font-size: 2rem;
    color: #000;
}

.totalMoney span {
    margin-left: 1rem;
}

.budgetBlock {
    box-sizing: border-box;
    display: flex;
    justify-content: space-evenly;
    padding: 1rem;
    width: 100%;
    margin: 0 auto;
}

.newTransaction input[type="text"],
.newTransaction select {
    background: transparent;
    text-align: center;
    border-radius: 2rem;
    width: 100%;
    margin-bottom: 2%;
    box-sizing: border-box;
    border: 1px solid #9e9e9e;
    font-size: 1rem;
    padding: 8px;
}

.newTransaction input[type="text"]:focus,
.newTransaction select:focus {
    outline: none;
}

.newTransaction input[name="price"] {
    margin-left: 2%;
}

.inputGroup {
    display: flex;
    justify-content: center;
}

.addTransaction {
    text-align: center;
    color: #fff;
    border-radius: 2rem;
    background-color: #425f9c;
    border: 0;
    margin: 1rem 0 0;
    padding: 1rem 4rem;
    font-size: 1rem;
    cursor: pointer;
    transition-duration: 0.4s;
}

.addTransaction:hover {
    background-color: #274177;
}

.addTransaction:focus {
    outline: none;
}

.lastesTransactions {
    background: linear-gradient(-25deg, #f0f0f0, #ffffff);
    width: 60%;
    max-height: 70vh;
    opacity: .9;
    border-radius: 1rem;
    overflow-x: hidden;
    overflow-y: auto;
    font-size: 1.2rem;
    -webkit-filter: drop-shadow(1px 3px 10px black);
            filter: drop-shadow(1px 3px 10px black);
}

.lastesTransactions ul {
    padding: 1rem;
}

.lastesText {
    background: #f0f0f0;
    margin: 0;
    padding: .5rem 1rem;
    position: sticky;
    top: -1px;
    border-bottom: 2px solid rgb(207, 207, 207);
    display: flex;
    justify-content: space-between;
}

.lastesText span {
    font-weight: bold;
    padding: 10px 0;
    margin: 0;
    font-size: 1.2rem;
}

ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

ul li {
    border-bottom: 1px solid #ddd;
    padding: 5px 0;
    display: flex;
    justify-content: space-between;
}

ul li:last-child {
    border-bottom: 0;
    padding: 5px 0 0;
}

.expense {
    color:#e40000;
    font-weight: bold;
}

.income {
    color:#006118;
    font-weight: bold;
}

li span {
    padding: 10px;
    text-align: center;
    cursor: pointer;
}

@media only screen and (max-width: 1000px) {
    .budgetBlock {
        flex-direction: column;
    }

    .lastesTransactions {
        width: 100%;
    }

    .welcomeBlock {
        padding-bottom: 2%;
    }
}

@media only screen and (min-width: 1500px) {

    .lastesTransactions {
        width: 70%;
    }
}

@media only screen and (min-width: 2100px) {

    .lastesTransactions {
        width: 80%;
    }
}
